@font-face {
    font-family: "MADE Soulmaze Italic";
    src: url('Soulmaze.otf');
}

@media (min-width: 480px) {

body{
  overflow: hidden;
}
}

.card{
  border: none;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.searchbar {
  padding-top:10px;
    position: relative;
    display: flex;
}

.searchbar > input {
    width: 100%;
}

.searchbar-list {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
}

.searchbar-item {
    padding: 5px;
    border: 1px solid darkgray;
    border-top: none;
    background: white;
    cursor: pointer;
}

.searchbar-item:hover {
    background: #e3e3e3
}